.form__item {
  position: relative;
  margin-bottom: 20px;
}

input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  height: 70px;
  width: 100%;
  padding-left: 40px;
  font-size: 14px;
  line-height: 22px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #271d67;
  background-color: #f2f5f8;
  border-radius: 5.0px;
  box-shadow: none;
}

input::placeholder {
  color: #90a8be;
}

input:focus::placeholder {
  color: transparent;
}

.form__submit {
  width: 100%;
}

.checkbox {
  display: none;
}

.label {
  display: block;
  position: relative;
  padding-left: 44px;
  font-size: 14px;
  line-height: 20px;
  font-weight: 600;
  color: #1c293a;
  cursor: pointer;
}

.label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 8px;
  width: 24px;
  height: 24px;
  border-radius: 5px;
  border: 2px solid #95c11f;
}

.checkbox:checked+.label:before {
  background-color: #95c11f;
  background-image: url(../img/check.svg);
  background-position: center;
  background-size: 90%;
}

.invalid {
  background-color: #f2f5f8;
  border: 2.0px solid #d40031;
  border-radius: 5.0px;
}

.error {
  position: absolute;
  font-size: 10px;
  line-height: 22px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #d40031;
}