/* ===== head ===== */

.head__top {
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  border-bottom: 2px solid #000;
}

.head__nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  position: relative;
  flex: 0 0 100px;
  margin-bottom: -22px;
  transition: .2s ease 0s;
}

.logo.scroll {
  flex: 0 0 80px;
  margin-bottom: 0px;
}

@media (min-width: 768px) {
  .logo {
    flex: 0 0 140px;
    margin-bottom: -30px;
    z-index: 3;
  }

  .logo.scroll {
    flex: 0 0 110px;
    margin-bottom: 0px;
  }
}

@media (min-width: 1300px) {
  .logo {
    flex-basis: 180px;
  }
}

.logo__link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


/* ===== burger ====== */



.nav__burger.active {
  /* order: 1; */
  /* color: #fff; */
}

.icon_menu {
  width: 40px;
}



.nav__burger.active .icon-cancel {
  display: inline;
}

@media (min-width: 768px) {
  .nav__burger {
    flex-grow: 2;
    /* position: relative;
    display: block; */
    /* z-index: 5; */
  }

  .icon_menu {
    transform: scaleX(-1);
  }
}


/* ======= menu ======== */

body.lock {
  overflow: hidden;

  @media (min-width: 768px) {
    overflow: auto;
  }
}


.navi {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  transition: .3s ease 0s;
  transform: translateY(-100%);

  &.active {
    transform: translateY(0%);
  }

  @media (min-width: 768px) {
    flex-grow: 1;
    position: static;
    width: auto;
    height: auto;
    padding: 0;
    overflow: hidden;
    transform: none;

    &.active {
      transform: none;
    }
  }

  @media (min-width: 1250px) {
    margin-left: 3%;
  }

  @media (min-width: 1400px) {
    margin-left: 6%;
  }
}

.navbar__body {
  width: 100%;
  padding: 40px 30px 0px 30px;
  background-color: #271d67;

  @media (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    height: 100%;
    padding: 0;
    background-color: #ffffff;

  }
}

.nav__list {
  position: relative;
  z-index: 2;
  /* margin: 40px 80px 0 60px; */

  @media (min-width: 768px) {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 42px 90px 42px 200px;
    background-color: #271d67;
    transition: .3s ease 0s;
    transform: translateY(-100%);

    &.active {
      transform: translateY(0%);
    }
  }

  @media (min-width: 1250px) {
    position: static;
    align-items: center;
    width: auto;
    height: auto;
    padding: 0;
    overflow: hidden;
    transform: translateY(0%);
    background-color: #ffffff;
  }
}



.navi li {
  margin-bottom: 22px;
}

.navi a {
  font-family: AAvanteBs;
  font-size: 20px;
  color: #fff;
}

.navi a.active {
  color: #95c11f;
}


@media (pointer: fine),
(min-width:0\0) {
  .navi a:hover {
    color: #95c11f;
  }
}

.navi a:active {
  color: #008d36;
}

@media (min-width: 768px) {
  .navi li {
    flex-shrink: 1;
    margin-bottom: 0;
  }

  .navi li:not(:last-child) {
    margin-right: 30px;
  }
}

@media (min-width: 1250px) {
  .nav__burger {
    display: none;
  }

  .nav__list li {
    /* margin-left: 30px; */
  }

  .nav__list a {
    font-size: 22px;
    color: #271d67;
  }

  .navi a:hover {
    color: #95c11f;
  }

  .navi a:active {
    color: #008d36;
  }
}






.head__city {
  margin-bottom: 40px;
}

@media (min-width: 768px) {
  .head__city {
    display: flex;
    align-items: center;
    margin-bottom: 0;
    /* margin-right: 30px; */
  }
}

.icon_map {
  display: none;
  flex-shrink: 0;
  width: 3em;
  height: 3em;
  margin-right: 10px;
}

.head-city__text {
  margin-bottom: 9px;
  margin-right: 15px;
  font-size: 14px;
  line-height: 22px;
  font-weight: 500;
  color: #90a8be;
}

.head-city__desc {
  font-weight: 500;
  color: #fff;
}

@media (pointer: fine),
(min-width:0\0) {
  .head-city__desc:hover {
    color: #95c11f;
  }
}

.head-city__desc:active {
  color: #008d36;
}

.city__drop_down {
  width: 10px;
  height: 5px;
  color: #95c11f;
  vertical-align: middle;
}

.head__link_phone {
  display: block;
  width: 100%;
  padding: 17px 30px;
  margin-bottom: 40px;
  font-size: 20px;
  line-height: 27px;
  font-weight: 700;
  color: #ffffff;
  background-color: #95c11f;
  border-radius: 5.0px;
}

.nav__close {
  display: block;
  width: 100%;
  height: 20px;
}

.div__close {
  margin-left: auto;
  margin-right: auto;
  width: 80px;
  height: 2px;
  background-color: #90a8be;
}

.icon_close {
  display: none;
  width: 20px;
  height: 20px;
  color: #95c11f;
}

@media (pointer: fine),
(min-width:0\0) {
  .icon_close:hover {
    color: #ffffff;
  }
}

.icon_close:active {
  color: #008d36;
}

@media (min-width: 768px) {
  .icon_map {
    display: block;
  }

  .head-city__text {
    display: block;
  }

  .head-city__desc {
    color: #1c293a;
  }

  .head__link_phone {
    margin-bottom: 0;
    width: auto;
    background-color: #271d67;
  }

  .nav__close {
    position: fixed;
    top: -100%;
    right: 30px;
    z-index: 3;
    width: auto;
    height: auto;
    background-color: transparent;
    transition: .2s ease 0s;
  }

  .nav__close.active {
    top: 45px;
  }

  .div__close {
    display: none;
  }

  .icon_close {
    display: block;
  }
}

.icon_phone {
  display: none;
}

@media (min-width: 992px) {
  .head__link_phone {
    padding: 16px 30px;
  }

  .icon_phone {
    display: inline;
  }
}

.icon_phone {
  width: 2em;
  height: 2em;
  margin-right: 20px;
  opacity: 0.4;
  vertical-align: middle;
}

.head__link_phone:hover .icon_phone {
  opacity: 1;
}