/* text.html */


.art {
  font-size: 14px;
}

.art__title_2 {
  font-family: AAvanteBs;
  font-size: 30px;
  line-height: 1;
  color: #7fc200;
}

.title_3 {
  font-family: AAvanteBs;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 400;
  color: #261769;
}

.art-top,
.art__col {
  margin-bottom: 40px;
}

@media (min-width: 768px) {
  .art {
    margin-bottom: 20px;
    font-size: 16px;
  }

  .art__title_2 {
    font-size: 40px;
  }

  .art__title_3 {
    font-size: 36px;
  }

  .art-top,
  .art__col {
    margin-bottom: 60px;
  }
}

@media (min-width: 1200px) {
  .art {
    margin-bottom: 100px;
    font-size: 18px;
  }

  .art__title_2 {
    font-size: 50px;
  }
}

.art-top {
  display: flex;
}

.art-top::before {
  content: "";
  display: block;
  flex-grow: 1;
  background-color: #271d67;
}

.art-top__wrap {
  flex: 1 1 1440px;
}

.art-top__container {
  position: relative;
  margin-left: 0;
  background-color: #261769;
}

.art-top__wrap_img {
  margin-left: -15px;
  margin-right: -15px;
}

.art-top__img {
  width: 100%;
}

.art-top__nav {
  padding-top: 45px;
  padding-bottom: 40px;
}

@media (min-width: 768px) {
  .art-top__wrap_img {
    margin-left: -30px;
    margin-right: -30px;
  }

  .art-top__nav {
    padding-top: 65px;
    padding-bottom: 60px;
  }
}

@media (min-width: 1200px) {
  .art-top__wrap_img {
    margin-left: -10px;
    margin-right: -10px;
  }

  .art-top__nav {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 65px 120px 60px;
    background-color: rgba(39, 29, 103, 0.7);
  }
}

.breadcrumb {
  display: flex;
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 1.2;
  font-weight: 700;
  text-transform: uppercase;
  color: #90a8be;
}

@media (min-width: 768px) {
  .breadcrumb {
    margin-bottom: 30px;
  }
}

.breadcrumb>li+li:before {
  content: "/";
  padding: 0 10px;
}

@media (pointer: fine),
(min-width:0\0) {
  .breadcrumb a:hover {
    color: #474d97;
  }
}

.breadcrumb a:active {
  color: #008d36;
}

.breadcrumb__icon {
  width: 20px;
  height: 17px;
}

.d-none {
  display: none;
}

@media (min-width: 768px) {
  .d-md-inline {
    display: inline;
  }
}

.art__text_blue {
  font-size: 16px;
  font-weight: 700;
  color: #271d67;
}

.art__text {
  margin-bottom: 30px;
}

.art__col_quote {}

.art__quote {
  padding: 40px 15px;
  margin: 0px -15px;
  background-color: #e7f0f7;
  border-radius: 5.0px;
}

.art-quote__icon {
  position: absolute;
  top: 0;
  transform: translatey(-40%);
  width: 40px;
  height: 25px;
  color: #261769;
}

.art-quote__text {
  font-size: 16px;
  font-weight: 700;
  color: #271d67;
}

.art-quote__name {
  margin-top: 20px;
  font-size: 16px;
  font-weight: 500;
  color: #90a8be;
}

@media (min-width: 768px) {
  .art__quote {
    margin: 0px -30px;
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media (min-width: 1200px) {
  .art__text_blue {
    font-size: 20px;
    line-height: 26px;
  }

  .offset {
    margin-left: 8.333333%;
  }

  .art__quote {
    margin: 0;
    padding-left: 8.333333%;
    padding-right: 8.333333%;
  }
}

.art__list li {
  display: flex;
  margin-bottom: 15px;
}

.art__list svg {
  flex-shrink: 0;
  width: 20px;
  height: 9px;
  margin-right: 10px;
  margin-top: 4px;
  color: #90a8be;
}

.art__img {
  margin-bottom: 20px;
}

/* ==== table ===== */

.art__col_table {
  /* width: 100%; */
  overflow: auto;
  margin-bottom: 40px;
  box-shadow: inset -60px 0px 60px -60px rgba(0, 0, 0, 0.40);
}

table {
  position: relative;
  z-index: -1;
  table-layout: fixed;
  width: 200%;
  border-collapse: collapse;
  border: 2.0px solid #e9f0f6;
  border-radius: 5.0px;
}

@media (min-width: 768px) {
  .art__col_table {
    margin-right: 0px;
    box-shadow: none;
  }

  table {
    width: 100%;
  }
}

thead {
  color: #261769;
  text-align: left;
  background-color: #e8f0f7;
}

thead th:nth-child(1) {
  width: 50%;
}

thead th:nth-child(2) {
  width: 16%;
}

thead th:nth-child(3) {
  width: 34%;
}

tr {
  border-bottom: 2.0px solid #e9f0f6;
}

th,
td {
  padding: 10px;
}

@media (min-width: 768px) {

  th,
  td {
    padding: 20px;
  }
}


.art__ol {
  counter-reset: num;
  list-style: none;
}

.art__ol li::before {
  counter-increment: num;
  content: counter(num);
  position: absolute;
  left: 0;
  top: -3px;
  padding: 5px 10px;
  font-size: 14px;
  line-height: 1.2;
  font-weight: 700;
  text-transform: uppercase;
  color: #271d67;
  background-color: #e9f0f6;
  border-radius: 5.0px;
}

.art__ol li {
  position: relative;
  padding-left: 40px;
}

.art__ol li:not(:last-child) {
  margin-bottom: 15px;
}

@media (min-width: 768px) {
  .art__ol li:not(:last-child) {
    margin-bottom: 20px;
  }
}


/* ===== swiper ===== */

.article__swiper {
  margin-bottom: 60px;
  padding-top: 30px;
  padding-bottom: 40px;
  background-color: #271d67;
}

.art__swiper {
  height: 250px;
  /* height: 30%; */

}

.art__swiper_slide {
  height: calc((100% - 20px) / 2);
  font-size: 16px;
  font-weight: 700;
  color: #ffffff;
}

.art__swiper_slide.swiper-slide-active {
  text-align: right;
  margin-bottom: 85px;
}

.art__arrow_carousel {
  margin-bottom: 0px;
}

.art__next_slide,
.art__prev_slide {
  position: relative;
  z-index: 3;
  margin-bottom: 20px;
  font-size: 16px;
  line-height: 22px;
  font-weight: 500;
  color: #86aac0;
}

.art__prev_slide {
  position: absolute;
  top: 50%;
  z-index: 3;
}

.art__next_slide svg {
  margin-left: 20px;
  color: #95c11f;
}

.art__prev_slide svg {
  margin-right: 20px;
  color: #95c11f;
  transform: rotate(180deg);
}



@media (min-width: 768px) {
  .article__swiper {
    margin-bottom: 80px;
    padding-top: 50px;
    padding-bottom: 60px;
  }

  .art__swiper {
    height: auto;
  }

  .art__swiper_slide {
    height: auto;
  }

  .art__prev_slide {
    position: static;
  }

  .art__swiper_slide {
    font-size: 18px;
  }

  .swiper-slide-next {
    text-align: right;
  }

  .art__swiper_slide.swiper-slide-active {
    text-align: left;
    margin-bottom: 0px;
  }
}


/* ===== art-news ===== */


.art-news {
  margin-bottom: 40px;
}

.art-news__wrap_img img {
  border-radius: 5px 5px 0 0;
}

.art-news__item {
  display: block;
  padding: 0px 20px 30px 20px;
  background-color: #e8f0f7;
  border-radius: 5.0px;
}

.art-news__swiper_slide {
  margin-bottom: 20px;
}

.art-news__wrap_img {
  position: relative;
  padding-bottom: 70%;
  margin: 0px -20px 20px -20px;
}

@media (min-width: 768px) {
  .art-news__swiper_slide:first-child .art-news__item {
    background-color: #fff;
  }

  .art-news__arrow_carousel {
    display: none;
  }

  .d-md-none {
    display: none;
  }
}

@media (min-width: 1200px) {
  .d-xl-block {
    display: block;
  }
}