/* ===== footer ===== */

.footer {
  padding: 40px 0;
  font-size: 14px;
  line-height: 22px;
  font-weight: 500;
  color: #90a8be;
  background-color: #271d67;
}

.footer__row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.footer__wrap_right {
  flex-basis: 100%;
}

.footer__logo {
  width: 177px;
  margin-bottom: 20px;
}

.footer__text {
  margin-bottom: 20px;
}

.footer__btn {
  width: 100%;
  margin-bottom: 20px;
}

.footer__socials {
  display: flex;
  justify-content: space-between;
}

.footer__socials li {
  margin-bottom: 0;
}

@media (min-width: 768px) {
  .footer {
    font-size: 16px;
  }

  .footer__wrap_left {
    flex-basis: 50%;
  }

  .footer__wrap_right {
    flex-basis: 50%;
    text-align: right;
  }

  .footer__btn {
    width: auto;
  }

  .footer__socials {
    justify-content: end;
  }

  .footer__socials li {
    margin-left: 10px;
  }
}

@media (min-width: 1200px) {
  .footer {
    padding-top: 100px;
  }

  .footer__row {
    align-items: center;
  }

  .footer__wrap_left {
    flex-basis: 45%;
    display: flex;
  }

  .footer__wrap_right {
    flex-basis: 46%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-basis: 50%;
    text-align: right;
  }

  .footer__logo {
    flex-shrink: 0;
    margin-right: 30px;
  }

  .footer__text {
    margin-bottom: 0;
  }

  .footer__btn {
    order: 1;
    margin-bottom: 0;
  }

  .footer__socials a {
    width: 60px;
    height: 60px;
  }

  .footer__socials svg {
    width: 26px;
    height: 26px;
  }
}

.copirite {
  margin-top: 40px;
}

