// ===== head__body ===== 

@if 1 {

  .head__body {
    overflow: hidden;
    display: flex;
  }

  .head__body::after {
    content: "";
    display: block;
    flex-grow: 1;
    background-color: #fff;
  }

  .head__inner {
    flex: 1 1 1440px;
  }

  @media (min-width: 768px) {
    .head__region {
      width: 100%;
      background-color: #271d67;
    }
  }

  .region__content {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
  }

  .region-content__text {
    margin-right: 40px;
    line-height: 22px;
    font-weight: 500;
    color: #ffffff;
  }

  .region-content__city {
    font-weight: 700;
    color: #95c11f;
  }

  .region__btn {
    width: 100%;
    padding: 20px 29px;
    line-height: 27px;
    font-weight: 700;
    color: #95c11f;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 5.0px;
    transition: 0.1s;
  }

  @media (pointer: fine),
  (min-width:0\0) {
    .region__btn:hover {
      color: #ffffff;
      background-color: #95c11f;
    }
  }

  .region__btn:active {
    color: #ffffff;
    background-color: #008d36;
  }


  .region__list {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 4;
    width: 100%;
    padding: 40px 30px 10px 30px;
    font-weight: 700;
    color: #95c11f;
    background-color: #271d67;
  }

  .region__title {
    font-family: AAvanteBs;
    font-size: 20px;
    line-height: 22px;
    color: #ffffff;
    font-weight: 400;
    margin-bottom: 35px;
  }

  .radio {
    display: none;
  }

  .radio+label:hover {
    color: #ffffff;
  }

  .radio+label {
    display: block;
    margin-right: 29px;
    margin-bottom: 30px;
  }

  .radio:checked+label {
    color: #008d36;
  }

  @media (min-width: 768px) {
    .region__btn {
      width: auto;
      margin-right: 10px;
    }

    .region__list {
      position: static;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px 0;
    }

    .region__title {
      display: none;
    }

    .radio+label {
      margin-bottom: 0px;
    }
  }



  .head__body {
    margin-top: 78px;
  }

  @media (min-width: 768px) {
    .head__body {
      margin-top: 110px;
    }
  }

  @media (min-width: 1300px) {
    .head__body {
      margin-top: 152px;
    }
  }

  .head__we {
    /* max-width: 1670px; */
    padding: 40px 0px 20px 0px;
    background: linear-gradient(0.0deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.0) 60%, rgba(0, 0, 0, 0.0) 100%), url(../img/head_bg2.jpg) no-repeat center /100% auto;
    background-color: #1b1b20;

  }

  @media (min-width: 768px) {
    .head__we {
      padding: 90px 0px 50px 0px;
    }
  }

  @media (min-width: 1200px) {
    .head__we {
      padding: 110px 0px 57px 0px;
      background-image: url(../img/head_bg1.jpg);
      background-position: right;
    }
  }

  .container_head {
    display: flex;
    justify-content: space-between;
  }

  @media (min-width: 1440px) {
    .container_head {
      margin-right: 0;
    }
  }



  .head-we__title {
    margin-bottom: 20px;
  }

  .head__text {
    margin-bottom: 185px;
    font-weight: 300;
    color: #90a8be;
  }

  .head__btn {
    margin-bottom: 20px;
  }

  @media (min-width: 768px) {
    .head__text {
      margin-bottom: 300px;
    }
  }

  @media (min-width: 1200px) {
    .head-we__title {
      width: 550px;
    }

    .head__text {
      width: 40%;
      margin-bottom: 40px;
    }

    .head__btn {
      margin-bottom: 40px;
    }
  }


  .link_more {
    display: inline-flex;
    align-items: center;
  }

  .link_img {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 70px;
    height: 70px;
    margin-right: 15px;
    background-image: url(../img/ellips.png);
  }

  .circle {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #95c11f;
    transition: .1s ease 0s;
  }

  @media (pointer: fine),
  (min-width:0\0) {
    .link_more:hover .circle {
      background-color: #3ba618;
    }
  }

  .link_more:active .circle {
    background-color: #077431;
  }

  .icon_more {
    height: 14px;
    height: 7px;
    color: #ffffff;
    transform: rotate(-90deg);
  }

  .link-more__text {
    font-size: 18px;
    line-height: 24px;
    font-weight: 300;
    color: #95c11f;
  }


  .head__socials {
    position: fixed;
    top: -100%;
    right: 30px;
    transition: top .3s ease 0s;
  }

  .head__socials.active {
    top: 40px;
  }

  @media (min-width: 768px) {
    .head__socials {
      position: static;
      margin-top: 130px;
      display: block;
    }
  }

  .socials li {
    margin-bottom: 5px;
  }

  .socials a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    color: #fff;
    background-color: rgba(242, 245, 248, 0.2);
    border-radius: 5.0px;
    transition: .1s ease 0s;
  }


  @media (pointer: fine),
  (min-width:0\0) {
    .socials a:hover {
      background-color: #95c11f;
    }
  }

  .socials a:active {
    background-color: #008d36;
  }

  .socials svg {
    width: 18px;
    height: 18px;
  }

  .white {
    flex: 1 1;
    background-color: #fff;
  }

  .hi {
    color: #fff;
  }
}



