/* ===== help ===== */

.help {
  padding-bottom: 40px;
  overflow: hidden;
  background-color: #95c11f;
}

@media (min-width: 768px) {
  .help {
    padding-bottom: 80px;
  }
}

@media (min-width: 1200px) {
  .help {
    padding-bottom: 0;
  }
}

.help__row {
  justify-content: space-between;
  align-items: center;
}

.arrow_wrap {
  position: relative;
}

.arrow_wrap::before {
  content: "";
  position: absolute;
  top: 0;
  left: -1000px;
  display: block;
  width: 1000px;
  height: 100%;
  background-color: #271d67;
}

.title_arrow {
  position: relative;
  z-index: 0;
  padding: 27px 0;
  margin-right: 20px;
  color: #95c11f;
  background-color: #271d67;
}

.title_arrow:before,
.title_arrow:after {
  content: "";
  position: absolute;
  right: 0;
  z-index: -1;
  height: 50%;
  width: 50%;
  background-color: #271d67;
}

.title_arrow:before {
  top: 0;
  transform: skewx(14deg);
  transform-origin: top;
}

.title_arrow:after {
  bottom: 0;
  transform: skewx(-14deg);
  transform-origin: bottom;
}

@media (min-width: 768px) {
  .title_arrow {
    padding: 30px 20px 30px 0px;
  }
}

@media (min-width: 1200px) {
  .title_arrow {
    display: flex;
    align-items: center;
    padding: 0;
    background-color: transparent;
  }

  .title_arrow:before,
  .title_arrow:after {
    content: none;
  }
}

.help__title {
  margin-bottom: 0;
  color: #7fc200;
  background-color: #271d67;
}

.help__title_w {
  margin-bottom: 0;
  color: #fff;
}

@media (min-width: 1200px) {
  .help__title {
    position: relative;
    z-index: 0;
    padding: 50px 30px 50px 0px;
    margin-right: 60px;
    color: #95c11f;
    background-color: #271d67;
  }

  .help__title:before,
  .help__title:after {
    content: "";
    position: absolute;
    right: 0;
    z-index: -1;
    height: 50%;
    width: 50%;
    background-color: #271d67;
  }

  .help__title:before {
    top: 0;
    transform: skewx(14deg);
    transform-origin: top;
  }

  .help__title:after {
    bottom: 0;
    transform: skewx(-14deg);
    transform-origin: bottom;
  }
}


.callback {
  margin-top: 30px;
  width: 100%;
  background-color: #271d67;
}

@media (min-width: 576px) {
  .callback {
    margin-top: 0px;
