  color: #ffffff;
}

.how__text {
  font-size: 14px;
  line-height: 20px;
  font-weight: 300;
  color: #ffffff;
}

[data-text] {
  transition: .5s ease .0s;
}

[data-text]:not(.show) {
  display: none;
}

[data-text]>p {
  padding-bottom: 20px;
}

@media (min-width: 768px) {
  .how__text {
    order: 1;
    font-size: 18px;
    line-height: 24px;
  }

  [data-text] {
    position: absolute;
    top: 20px;
  }
}

@media (min-width: 1200px) {
  [data-text] {
    top: 40px;
    width: 48%;
  }
}


.arrow {
  position: relative;
  z-index: 0;
  display: flex;
  align-items: center;
  height: 100%;
  padding: 30px 10px 30px 30px;
}

.arrow::before,
.arrow::after {
  content: "";
  position: absolute;
  left: 0;
  z-index: -1;
  height: 50.5%;
  width: 100%;
  background: linear-gradient(90deg, #95c11f 50%, #fff 50%) #fff right/ 220%;
  transition: background-position .5s ease 0s;
}

.arrow.active.arrow::before,
.arrow.active.arrow::after {
  background-position: left;
  background-color: #95c11f;
  transition: .5s ease .2s;
}

.arrow:before {
  top: 0;
  transform: skewx(14deg);
  transform-origin: top;
}

.arrow:after {
  bottom: 0;
  transform: skewx(-14deg);
  transform-origin: bottom;
}

.arrow_start {
  background-color: #fff;
  border-left: 4.0px solid transparent;
  transition: background-color .25s ease .3s;
}

.arrow_start.active {
  background-color: #95c11f;
  transition: .15s ease .25s;
}

.arrow_end {
  margin-right: -10px;
  border-right: 4px solid transparent;
  overflow: hidden;
}

.arrow_end.active {
  border-color: #95c11f;
}

.arrow_end:before,
.arrow_end:after {
  width: 120%;
}


@media (min-width: 768px) {
  .arrow {
    padding: 43px 30px 43px 40px;
  }

  .arrow_start {
    padding-left: 20px;
  }

  .arrow_end {
    margin-right: 0px;
  }
}

@media (pointer: fine),
(min-width:0\0) {

  .arrow:hover::before,
  .arrow:hover::after {
    border: 4.0px solid #95c11f;
  }

  .arrow:hover::before {
    border-bottom: 0;
  }

  .arrow:hover::after {
    border-top: 0;
  }

  .arrow_start:hover::before,
  .arrow_start:hover::after {
    border-left: 0;
  }

  .arrow_start:hover,
  .arrow_end:hover {
    border-color: #95c11f;
  }
}

.arrow:active::before,
.arrow:active::after,
.arrow_start:active,
.arrow_end:active {

  border-color: #008d36;
}

.arrow:active .arrow__desc,
.arrow:active .arrow__desc span {
  color: #008d36;
}




.arrow__desc {
  display: flex;
  align-items: center;
  color: #95c11f;
}

.arrow.active .arrow__desc,
.arrow.active .arrow__desc span {
  color: #fff;
}

.arrow__text {
  font-size: 20px;
  line-height: 1.2;
  font-weight: 700;
  color: #271d67;
}

.arrow__text span {
  display: block;
  margin-top: 18px;
  font-size: 14px;
  line-height: 1.2;
  font-weight: 700;
  text-transform: uppercase;
  color: #95c11f;
}


.arrow__desc svg {
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  margin-right: 15px;
}

@media (min-width: 768px) {
  .arrow__desc svg {
    width: 80px;
    height: 80px;
    margin-right: 30px;
  }
}



/* ===== we ===== */

.we {
