// ====  modal =====

.hidden {
  display: none
}

.no-scroll {
  overflow-y: auto !important;
}

.has-dialog {
  overflow: hidden;
}

/* modal backdrop */

.dialog-backdrop {
  display: none;
  position: fixed;
  z-index: 20;
  overflow-y: auto;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background: rgba(25, 25, 25, 0.9);
}

.dialog-backdrop.active {
  display: block;
}


/* modal window */

[role="dialog"] {
  position: absolute;
  top: 40px;
  left: 50%;
  transform: translateX(-50%);
  padding-bottom: 30px;
  width: calc(100% - (15px * 2));
}

@media (min-width: 576px) {
  [role="dialog"] {
    width: 460px;
  }
}

@media (min-width: 1200px) {
  [role="dialog"] {
    width: 940px;
  }
}

.modal__content {
  display: flex;
  border-radius: 5.0px;
  background-color: #fff;
}

.modal__logo {
  display: none;
  align-items: flex-end;
  flex-shrink: 0;
  width: 480px;
  padding: 60px;
  background-color: #271d67;
  background-image: url(../img/logo_modal.svg);
  background-repeat: no-repeat;
  background-size: 60%;
  background-position: top 100px center;
}

.modal-logo__text {
  margin-bottom: 0;
}

@media (min-width: 1200px) {
  .modal__logo {
    display: flex;
  }
}

.modal__form {
  padding: 40px 15px;
}

@media (min-width: 576px) {
  .modal__form {
    padding: 60px;
  }
}

.modal__title {
  margin-bottom: 40px;
  font-family: AAvanteBs;
  font-size: 40px;
  line-height: 1.2;
  font-weight: 400;
  color: #271d67;
}

.modal__text {
  font-size: 14px;
  line-height: 20px;
  font-weight: 700;
  text-transform: uppercase;
  color: #90a8be;
}

.modal-form__text {
  margin-bottom: 40px;
}

@media (min-width: 1200px) {
  .modal-form__text {
    display: none;
  }
}

.modal__button_close {
  position: absolute;
  top: -30px;
  right: 0px;
  color: #95c11f;
}

@media (min-width: 768px) {
  .modal__button_close {
    right: -30px;
  }
}

.modal__button_close:hover {
  color: #fff;
}

.modal__icon_cancel {
  width: 20px;
  height: 20px;
}

