/* SVG icon */
.icon {
  display: inline-block;
  width: 4em;
  height: 4em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}
 
/* .wrap */
body {
  max-width: 1920px;
  margin-right: auto;
  margin-left: auto;
	overflow-x: hidden;
}

.img_cover {
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
  font-family: 'object-fit: cover;';
}

.title_1 {
  font-family: AAvanteBs;
  font-size: 30px;
  line-height: 1.2;
  font-weight: 400;
  text-transform: uppercase;
  color: #95c11f;
}

.title_2 {
  margin-bottom: 20px;
  font-family: AAvanteBs;
  font-size: 30px;
  line-height: 1.2;
  font-weight: 400;
}

@media (min-width: 768px) {
  .title_1 {
    font-size: 50px;
  }

  .title_2 {
    margin-bottom: 40px;
    font-size: 40px;
  }
}

@media (min-width: 1200px) {
  .title_1 {
    font-size: 60px;
  }

  .title_2 {
    font-size: 50px;
  }
}

.text_blue {
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 1.2;
  font-weight: 700;
  text-transform: uppercase;
  color: #90a8be;
}

.btn {
  display: inline-block;
  border-radius: 5.0px;
  font-size: 14px;
  line-height: 22px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #ffffff;
  padding: 23px 26px;
  transition: .1s ease 0s;
}

.hover_blue {
  color: #fff;
  background-color: #271d67;
}

@media (pointer: fine),
(min-width:0\0) {
  .hover_blue:hover {
    color: #95c11f;
  }
}

.hover_blue:active {
  background-color: #42378a;
}

.btn_green {
  background-color: #95c11f;
}

@media (pointer: fine),
(min-width:0\0) {
  .btn_green:hover {
    background-color: #008d36;
  }
}

.btn_green:active {
  background-color: #077431;
}